<template>
  <div class="activity-manage-page">
    <navigation-bar
      class="activity-nav-bar"
      :pageName="title"
      :border="false"
      @onLeftClick="onBack"
    />
    <div class="activity-content">
      <van-pull-refresh
        v-model="refreshing"
        @refresh="onRefresh"
      >
        <van-list
          v-model="loading"
          :error.sync="error"
          error-text="请求失败，点击重新加载"
          :finished="finished"
          finished-text="我是有底线的"
          @load="onLoad"
          v-show="!(contentLoaded && activityList.length == 0)"
          >
            <div class="activity-content-cell " v-for="(item, index) in activityList" :key="index" @click="goDetail(item)">
              <div class="activity-timer">{{item.CREATED_TIME}}</div>
              <div class="cell-content">
                  <div class="activity-content-top">
                      <div>{{item.ACTI_NAME}}</div>
                      <img :src="activityCurrentStatus(item)" alt="">
                  </div>
                  <div class="activity-content-backgroundImg">
                      <img :src="activityBackgroundImg(item.PIC_URL)" alt="">
                      <div v-show="activityIsOver(item)">报名已结束</div>
                  </div>
                  <div class="activity-indate">有效期 {{item.START_TIME}} 至 {{item.END_TIME}}</div>
                  <div class="activity-signin-progress">
                      <progress :value="activitySignupPercentage(item)" max="100"></progress>
                      <div>{{item.ENROL_CNTS}}/{{item.LIMIT_MAX}}人</div>
                  </div>
                  <div class="activity-lookdeatil">
                    查看详情
                    <van-icon name="arrow" size="12px"/>
                  </div>
              </div>
            </div>
        </van-list>
        <div class="no-list" v-show="contentLoaded && activityList.length == 0">
          <img :src="noSourceImg" alt="">
          <div>暂无活动</div>
        </div>
      </van-pull-refresh>
    </div>
  </div>
</template>

<script>
  import {
    getActivityListService,
  } from "../../services/activityService";
  import {store, storage} from "../../utils/store";

  import Vue from 'vue';
  import {Toast, List, PullRefresh, Icon } from 'vant';
  Vue.use(Toast).use(List).use(PullRefresh).use(Icon);
  import NavigationBar from "../title/NavigationBar";

  // 暂无数据图片
  import noSourceImg from '@/assets/imgs/workPlan/noSource.png'
  import inProgress from '@/assets/imgs/activity/inProgress.png'
  import noSignUp from '@/assets/imgs/activity/noSignUp.png'
  import over from '@/assets/imgs/activity/over.png'
  import noStart from '@/assets/imgs/activity/noStart.png'
  import phone from '@/assets/imgs/activity/phone.png'
  import signUp from '@/assets/imgs/activity/signUp.png'
  import defaultback from '@/assets/imgs/activity/defaultback.png'

  export default {
    name: "activityManage",
    components: { NavigationBar },
    data() {
      return {
        active: 0,
        title: '活动管理',
        activityList: [],
        refreshing: false,
        loading: false,
        finished: false,
        error: false,
        // 当前页码值
        pageNum: 1,
        // 每页个数
        pageSize: 10,
        // 内容已加载
        contentLoaded: false,

        //图片
        noSourceImg: noSourceImg,
        noStart: noStart,
        inProgress: inProgress,
        noSignUp: noSignUp,
        over: over,
        defaultback: defaultback,
      };
    },
    computed: {
        // 返回活动当前状态图片
        activityCurrentStatus() {
           return function(item) {
            let statusImg = noStart;
            let nowDate = new Date(this.getNowDate().replace(/\-/g, "\/")).getTime();
            let startDate = new Date (item.START_TIME.replace(/\-/g, "\/")).getTime();
            let endDate = new Date (item.END_TIME.replace(/\-/g, "\/")).getTime();
            if(startDate > nowDate){
              statusImg = noStart;
            }
            else if(startDate <= nowDate && endDate >= nowDate){
              statusImg = inProgress;
            }else if(endDate < nowDate){
              statusImg = over;
            }
            return statusImg;
           }
        },
        // 返回活动当前报名是否已结束
        activityIsOver() {
           return function(item) {
            let nowDate = new Date(this.getNowDate().replace(/\-/g, "\/")).getTime();
            let endDate = new Date (item.END_TIME.replace(/\-/g, "\/")).getTime();
            return endDate < nowDate;
           }
        },
        // 活动封面图
        activityBackgroundImg() {
          return function(picUrl) {
            return picUrl && picUrl.length > 0 ? picUrl : defaultback;
          }
        },
        // 活动报名人数百分比计算
        activitySignupPercentage() {
          return function(item) {
            let totalPeople = item.LIMIT_MAX;//拿到总人数
            let signPeople = item.ENROL_CNTS;//拿到报名人数
            let percentage = Math.round(Number(signPeople)/Number(totalPeople) * 10000) / 100.00;
            return percentage;
          }
        }
    },
    methods: {
        // 获取当前时间
        getNowDate() {
          var date = new Date();
          var seperator1 = "-";
          var seperator2 = ":";
          var month = date.getMonth() + 1;
          var strDate = date.getDate();
          if (month >= 1 && month <= 9) {
            month = "0" + month;
          }
          if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
          }
          var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
            + " " + date.getHours() + seperator2 + date.getMinutes()
            + seperator2 + date.getSeconds();
          return currentdate;
      },
      // 获取活动数据
      getActivityList(isRefresh) {
        let custNews = store.get('custNews');
        custNews = custNews ? custNews: storage.getStorage("custNews");
        let requestData = {
          AREA_CODE: custNews.AREA_CODE,
          USER_CODE: custNews.USER_CODE,
          PAGE: this.pageNum.toString(),
          ROWS: this.pageSize.toString(),
        };
        getActivityListService(requestData)
        .then(noteData => {
          this.loading = false;
          if (isRefresh) {
            this.finished = false;
            this.refreshing = false;
            this.activityList = [];
          }
          noteData.ROWS.forEach(element => {
            this.activityList.push(element);
          });
          // 加载完成
          if (this.pageNum * this.pageSize >= noteData.TOTAL) {
            this.finished = true;
          }
          this.pageNum++;
          this.contentLoaded = true;
        })
        .catch(error => {
          this.loading = false;
          this.refreshing = false;
          this.error = true;
          Toast.fail(error);
          this.contentLoaded = true;
        });
      },
      // 加载更多
      onLoad() {
        this.error = false;
        this.loading = true;
        this.getActivityList(false);
      },
      //下拉刷新
      onRefresh() {
        this.error = false;
        this.loading = false;
        this.pageNum = 1;
        this.getActivityList(true);
      },
      // 跳转到详情页面
      goDetail: function(item) {
          store.set("activityDetailCode", item.ACTI_CODE)
          this.$router.push({
            name: "activityDetail",
          });
        },
        onBack() {
          history.back();
        },
      }
  };
</script>

